html {
  font-size: 62.5%;
}

img {
  object-fit: contain;
  max-width: 100%;
  height: auto;
  display: block;
}

.toolbar .toolbar-icon:before {
  width: 2rem;
}

.toolbar .toolbar-icon {
  margin: 0;
  font-size: 1.3rem;
}

@media only screen and (width >= 36em) {
  .toolbar .toolbar-bar .toolbar-tab > .toolbar-icon:before {
    width: 2rem;
  }
}

.toolbar .toolbar-toggle-orientation button {
  width: 3.9rem;
  height: 3.9rem;
}

.dialog-off-canvas-main-canvas {
  grid-template-rows: auto 1fr auto;
  grid-template-columns: minmax(0, 1fr);
  min-height: 100vh;
  display: grid;
}

.dialog-off-canvas-main-canvas * {
  box-sizing: border-box;
}

.toolbar-horizontal .dialog-off-canvas-main-canvas, .toolbar-vertical .dialog-off-canvas-main-canvas {
  min-height: calc(100vh - 39px);
}

.toolbar-horizontal.toolbar-tray-open .dialog-off-canvas-main-canvas {
  min-height: calc(100vh - 79px);
}

header, main, footer {
  background-color: #000;
}

.last-m-0 > :last-child {
  margin: 0;
}

.divider-12 {
  margin: 0 0 1.2rem;
}

.divider-16 {
  margin: 0 0 1.6rem;
}

.divider-24 {
  margin: 0 0 2.4rem;
}

.divider-small {
  margin: 0 0 4rem;
}

.divider-large {
  margin: 0 0 8rem;
}

.margin-auto {
  margin: auto;
}

@media only screen and (width <= 599px) {
  .divider-large {
    margin: 0 0 4rem;
  }

  .divider-small--mobile-24 {
    margin: 0 0 2.4rem;
  }
}

body {
  font-family: Arial, Helvetica, sans-serif;
}

h1 {
  color: #fff;
  margin: 0 0 2.4rem;
  font-size: 4.6rem;
  font-style: normal;
  font-weight: 700;
  line-height: 130%;
}

h2 {
  color: #fff;
  margin: 0 0 2.4rem;
  font-size: 4.1rem;
  font-style: normal;
  font-weight: 700;
  line-height: 130%;
}

h3 {
  color: #fff;
  margin: 0 0 2.4rem;
  font-size: 3.6rem;
  font-style: normal;
  font-weight: 700;
  line-height: 130%;
}

h4 {
  color: #fff;
  margin: 0 0 2.4rem;
  font-size: 3rem;
  font-style: normal;
  font-weight: 700;
  line-height: 130%;
}

h5 {
  color: #fff;
  margin: 0 0 2.4rem;
  font-size: 2.5rem;
  font-style: normal;
  font-weight: 700;
  line-height: 130%;
}

h6 {
  color: #fff;
  margin: 0 0 2.4rem;
  font-size: 2rem;
  font-style: normal;
  font-weight: 700;
  line-height: 130%;
}

p, a, ul, ol {
  color: #fff;
  margin: 0 0 2.4rem;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 400;
  line-height: 160%;
}

a {
  color: #fff;
  margin: 0;
  text-decoration: underline;
}

a:hover {
  text-decoration: none;
}

#toolbar-administration li, .toolbar-menu li, .contextual-links li {
  margin: 0;
  font-size: 1.3rem;
  display: block;
}

#toolbar-administration li:before, .toolbar-menu li:before, .contextual-links li:before {
  content: none;
}

#toolbar-administration a, .toolbar-menu a {
  font-size: 1.3rem;
}

.grey-bg {
  background-color: #dddfe0;
}

.audio-sample--teaser {
  color: #fff;
  align-items: center;
  gap: 4rem;
  display: flex;
}

.audio-sample--teaser .label p {
  margin: 0;
}

.audio-samples--overview {
  width: fit-content;
}

.button {
  cursor: pointer;
  border-radius: .4rem;
  height: fit-content;
  padding: 1.2rem 1.6rem;
}

.button a {
  text-decoration: none;
}

.button:hover {
  box-shadow: 0 3px 8px #00758659;
}

.hamburger {
  display: none;
}

@media only screen and (width <= 767px) {
  .hamburger {
    cursor: pointer;
    border-radius: .4rem;
    place-content: center;
    justify-self: end;
    height: 100%;
    padding: 1.2rem 1.6rem;
    display: grid;
  }

  .hamburger .bar {
    background-color: #000;
    width: 1.6rem;
    height: 2px;
    margin: 2px auto;
    transition: all .3s ease-in-out;
    display: block;
  }

  .hamburger.is-active span:first-child {
    transform: translateY(7px)rotate(45deg);
  }

  .hamburger.is-active span:nth-child(2) {
    opacity: 0;
  }

  .hamburger.is-active span:nth-child(3) {
    transform: translateY(-5px)rotate(-45deg);
  }
}

.hero-banner__content img {
  z-index: -2;
  filter: brightness(.8);
}

.header__navigation {
  grid-template-columns: 20% auto;
  align-items: center;
  padding: 2rem 0;
  display: grid;
}

.menu {
  padding: 1rem;
}

.main-menu {
  grid-auto-flow: column;
  gap: 2rem;
  width: fit-content;
  margin: 0 auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.main-menu__li {
  width: fit-content;
}

.main-menu__li a {
  color: #fff;
  font-size: 2rem;
}

.repertoar-background, .repertoar-overview {
  position: relative;
}

.repertoar-overview__content {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.repertoar-overview__content h2 {
  text-align: center;
}

.song-teaser__content {
  color: #fff;
  white-space: nowrap;
  gap: 1rem;
  display: flex;
}

.song-teaser__content .interpret {
  font-weight: 700;
}

.song-teaser__content .label {
  font-style: italic;
}

.song-teaser__content > div {
  font-size: 2rem;
}

.two-col-wrapper__content {
  grid-template-columns: 50% 50%;
  gap: 4rem;
  padding: 2rem;
  display: grid;
}

.two-col__content {
  grid-template-columns: 50% 50%;
  display: grid;
}

.two-col__content .col {
  padding: 1rem;
}

.two-col__content .col:first-of-type {
  place-items: center;
  display: grid;
}

.two-col__content .col:first-of-type p {
  border: 2px solid #fff;
  border-radius: 20px;
  padding: 2rem;
  font-size: 2rem;
}

.two-col__content .col:nth-child(2) {
  text-align: center;
}

.two-col__content .col:nth-child(2) p {
  font-size: 2rem;
}

.view-repertoar-items {
  background: #000;
  grid-template-columns: 1fr 1fr;
  column-gap: 15rem;
  padding: 2rem;
  display: grid;
  position: relative;
}

.view-repertoar-items:after {
  content: "";
  background-color: #fff;
  width: 2px;
  height: 81%;
  position: absolute;
  top: 50%;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, -50%);
}

.article-3 {
  grid-template-columns: repeat(3, 1fr);
  gap: 2.4rem;
  display: grid;
}

.article-3--same-row-h {
  grid-auto-rows: 1fr;
}

.article-3--news {
  gap: 4rem 8rem;
}

.article-3--news .line {
  content: "";
  border-bottom: 1px dashed #000;
  grid-column: 1 / -1;
  margin: 0 0 4rem;
}

.line-view-mode .article-3 {
  grid-template-columns: 1fr !important;
}

@media only screen and (width <= 1099px) {
  .article-3 {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media only screen and (width <= 599px) {
  .article-3 {
    grid-template-columns: 1fr;
  }

  .article-3--mobile-gap-8 {
    gap: .8rem;
  }
}

.article-4 {
  grid-gap: 2.4rem;
  grid-template-columns: repeat(4, 1fr);
  display: grid;
}

@media only screen and (width <= 1099px) {
  .article-4 {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media only screen and (width <= 767px) {
  .article-4 {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media only screen and (width <= 459px) {
  .article-4 {
    grid-template-columns: 1fr;
  }

  .article-4--mobile-gap-8 {
    gap: .8rem;
  }
}

